.menubar {
  color: rgb(238, 238, 238);
  background-color: rgba(167, 167, 167, .5);
  margin-top: calc((var(--app-padding) - 8px) * -1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  border-radius: var(--border_radius);
  padding: var(--app-padding) 0;
}

.menubar-left {
}

.menubar-right {
  
}

.menubar h1 {
  font-size: 3em;
  text-align: center;
  color: #fff; /* Цвет текста */
  position: relative;
  /* margin: 50px 0; */
}

/* Создание неонового эффекта */
h1::before {
  content: '[[PC_APP]]';
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  color: #008183; /* Цвет неона */
  text-shadow: 0 0 10px #007c7e, 0 0 20px #007c7e, 0 0 30px #8b008d; /* Тень для создания свечения неона */
  opacity: 0.8;
  -webkit-animation: neonGlow 1.5s infinite alternate;
          animation: neonGlow 1.5s infinite alternate; /* Анимация мерцания неона */
}

@-webkit-keyframes neonGlow {
  to {
    opacity: 0.4;
    text-shadow: 0 0 5px #007c7e, 0 0 10px #007c7e, 0 0 15px #8b008d; /* Изменение тени для мерцания */
  }
}

@keyframes neonGlow {
  to {
    opacity: 0.4;
    text-shadow: 0 0 5px #007c7e, 0 0 10px #007c7e, 0 0 15px #8b008d; /* Изменение тени для мерцания */
  }
}
.menubar h2 {
  display: inline;
  font-size: 1em;
}

.newgame {
  border: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: rgba(0,0,0,0);
  color: white;
  font-size: 2em;
  border: 1px solid rgba(221, 221, 221, .2);
  background-color: rgba(87, 87, 87, 0.8);
  border-radius: var(--border_radius);
  cursor: pointer;
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
  margin-bottom: 20px;
}
.newgame span {
  font-size: 16px;

}

.newgame:hover {
  background-color: rgba(221, 221, 221, 0);
  border-color: rgba(221, 221, 221, .8);
}

.newgame:focus {
  outline: none;
}

@media screen and (max-width: 360px) {
  .menubar h1 {
    font-size: 1.5em;
  }
  
  .menubar h2 {
    font-size: 0.75em;
  }
  
  .newgame {
    font-size: 1.5em;
  }
}


