:root {
  --highlight-color: rgb(37, 192, 50);
  --primary-color: rgb(46, 156, 202);
  --secondary-color: rgb(127, 41, 138, .6);
  --primary-bgcolor: #008183;
  --bg-gradient: linear-gradient(90deg, #007c7e 0%, #8b008d 100%);
  --primary-bgcolor-90: rgba(37, 39, 77, 0.9);
  --outline-color: rgb(255, 255, 255);

  --border_radius: 10px;

  --app-padding: 2rem;
  --card-gap: 5px;
  --menubar-height: 3rem;
  --max-card-dim: 200px;
  --max-img-dim: calc(var(--max-card-dim) * 0.8);
  --corner-rad: calc(var(--max-card-dim) * 0.05);
  --num-cols: 6;
  --num-rows: 3;
}

@media (max-width: 1300px) {
  :root {
    --max-card-dim: 150px;
  }
}
@media (max-width: 991px) {
  :root {
    --max-card-dim: 100px;
  }
}
@media (max-width: 715px) {
  :root {
    --max-card-dim: 80px;
  }
}
@media (max-width: 637px) {
  :root {
    --max-card-dim: 100px;
  }
}
@media (max-width: 375px) {
  :root {
    --max-card-dim: 70px;
  }
}

@font-face {
  font-family: 'PT Sans';
  src: url('../fonts/PTSans-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'PT Sans';
  src: url('../fonts/PTSans-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: var(--app-padding);
  font-family: 'PT Sans', 'Verdana', 'sans-serif';
  background-color: var(--primary-bgcolor);
  background-image: var(--bg-gradient);
  /* overflow: hidden; */
  min-height: 100vh;
}


