.popSearches {
  display: block;
  width: 80%;
}

.popSearches-spinner {
  background-color: white;
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
  border-radius: 2.5px;
  -webkit-animation: an-spinner 0.75s infinite ease-in-out;
  animation: an-spinner 0.75s infinite ease-in-out;
}

.popSearches-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}


